'use client';
import Link from "next/link";
import Image from "next/image";
import PokeLogo from "../../public/ScorpionWebPoke.png"
import "../app/css/Navbar.css";
import { useState } from "react";
export default function Navbar(){
    const  [isLogged, setLogged] = useState(false);
    return(
        <nav>
            <div className="NavbarLogo">
                <Link href="/"><Image src={PokeLogo} width={100} height={100} alt="PokeLogo"/></Link>
                <h1>PokeScorpion</h1>
            </div>
            <div className="Rutas">
                <Link href="/">Qué es PokeScorpion?</Link>
                <Link href="/">Inicia Sesión</Link>
                <Link href="/">Registrate!</Link>
            </div>
        </nav>
    );
}